<template>
  <v-container>
    <v-row justify="center">
      <v-col sm="6">
        <v-alert class="subtitle-2" color="cyan" border="left" outlined dense icon="mdi-twitter">
            #4 -  
        </v-alert>
      </v-col>
      <v-col sm="6">
        <v-btn @click="fetchData" text color="red"></v-btn>
      </v-col>
    </v-row>
    <v-row justify="center">
      <v-col sm="12">
        <template>
          <v-data-table dense :headers="headers" :items="items" item-key="id" class="elevation-1"></v-data-table>
        </template>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    items: [],
    headers: [
      {
        text: 'ID',
        align: 'start',
        sortable: false,
        filterable: false,
        value: 'id',
      },
      { text: '', value: 'username' },
      { text: '', value: 'website' },
      { text: '', value: 'email' },
      { text: '', value: 'name' },
      { text: '', value: 'phone' },
    ],

  }),
  created() {
    this.fetchData()

  },
  methods: {
    fetchData: function () {
      var vm = this;
      this.$axios.get('https://jsonplaceholder.typicode.com/users/')
        .then(function (response) {
          vm.items = response.data 
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  }
}
</script>

